.header {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-content: center;
  padding-right: 50px;
  box-shadow: 0 2px 4px rgba($color: #000, $alpha: 0.05);

  &__home {
    margin: 10px 50px;
    width: 40px;
    height: 40px;
  }

  &__home--margin {
    margin: 10px 50px;
  }
}
